body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  color: #012a4a;
  background-color: #e9fbff;
  font-family: "Rubik", sans-serif;
  line-height: 1.6;
}

header {
  padding: 20px 0;
  background: #0e1c3d; /* Slightly darker tone */
}

#navMenu {
  display: none; /* By default, the menu is hidden */
  position: absolute; /* Absolute positioning relative to the header */
  right: 0; /* Aligns to the right edge of the header */
  top: 14%; /* Places it right below the header/hamburger menu */
  background-color: rgba(0, 0, 0, 0.7); /* Using a semi-transparent black */
  border-radius: 5px; /* Gives the menu rounded edges */
  z-index: 100; /* Ensures the menu is always on top */
  width: 200px; /* Set a specific width */
  height: auto; /* Allow it to expand as per content */
  max-height: 300px; /* Just to ensure it doesn't grow too big */
  overflow-y: auto; /* Allow scrolling if the content exceeds the max height */
}

#navMenu.active {
  display: block; /* When the menu has the 'active' class, it becomes visible */
}

#navMenu ul {
  list-style-type: none; /* Removes bullet points */
  margin: 0; /* Removes default margin */
  padding: 0; /* Removes default padding */
}

#navMenu li {
  padding: 8px 12px; /* Gives padding to each item */
}

#navMenu li a {
  text-decoration: none; /* Removes the default underline of hyperlinks */
  color: white; /* Sets the link color to white */
  font-weight: bold; /* Makes the text bold */
}

#navMenu li a:hover {
  text-decoration: underline; /* Underlines the link when hovered */
}

#logo {
  width: 200px;
  padding-left: 15px;
}

.hamburger-menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 30px; /* Width of the hamburger */
  height: 22px; /* Total height of the hamburger, adjust based on space between bars and their individual heights */
  cursor: pointer;
  position: absolute; /* To position it at the top right corner */
  top: 30px; /* Adjust to vertically center or give desired space from the top */
  right: 15px; /* Space from the right edge */
}

.bar {
  width: 100%; /* Take the full width of the parent i.e., .hamburger-menu */
  height: 4px; /* Height of individual bars */
  background-color: #fff; /* Color of the bars, making it white so it's visible on a darker header */
  border-radius: 2px; /* Rounded corners, optional */
}

#bud {
  width: 500px;
  transition: transform 0.4s ease-in-out, box-shadow 0.4s ease-in-out; /* Smooth transition */
}

#bud:hover {
  transform: scale(1.05); /* Slight zoom on hover */
}

.hero {
  display: flex;
  align-items: center;
  justify-content: flex-start; /* Adjusted to center the content wrapper */
  font-size: 30px;
  position: relative;
  margin-bottom: 0px;
  margin-top: 0px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-color: #e9fbff;
  clip-path: url(#curvedPath);
}

.hero-text-wrapper {
  /* New class for the text container */
  align-self: center; /* Ensures vertical centering */ /* Starts the content from the center of the .hero */
  position: relative; /* Make sure the text is above the gradient overlay */
  z-index: 2;
}

.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #e9fbff;
  background: linear-gradient(
    to right,
    rgba(233, 251, 255, 0.6),
    /* #e9fbff with 60% opacity */ rgba(10, 31, 60, 0.6)
      /* #0a1f3c with 60% opacity */
  );

  z-index: 0; /* Gradient remains behind the text */
}

.hero img,
.hero .action,
.hero .popup {
  position: relative;
  z-index: 1;
}

.hero h1 {
  letter-spacing: 0.8px;
  line-height: 1;
  color: #ffffff;
}

.hero p {
  letter-spacing: 0.6px;
  line-height: 0.2;
  color: #ffffff;
}

span {
  color: #00a5cf;
}

.action {
  text-align: left;
  padding: 20px;
  font-weight: 600;
  font-size: 25px;
}

#light {
  font-size: 20px;
  color: #ffffff; /* Muted color for secondary text */
}

button {
  display: inline-flex; /* This makes the button content flex items */
  align-items: center; /* Vertically center the image and text */
  padding: 5px 40px;
  margin: 20px 0;
  background: #0a1f3c;
  border: none;
  color: #fff;
  font-size: 22px;
  font-weight: bold;
  border-radius: 10px;
  transition: all 0.5s ease;
}

/* Styles for the image */
#buttonIcon {
  height: 60px; /* Adjust based on your preference */
  width: auto; /* Maintain aspect ratio */
  margin-right: 2px;
  margin-left: -20px; /* Add some spacing between the icon and the text */
}

.emily-button:hover {
  cursor: pointer;
  transform: scale(1.05); /* Subtle button growth on hover */
}

.info-box {
  padding: 20px;
  font-size: 24px;
  border-radius: 12px;
  background: #f6f6f6;
  margin-right: 20px;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  height: 250px; /* Set a fixed height */
  display: flex;
  flex-direction: column;
  justify-content: center; /* Vertically centers the items */
  align-items: center; /* Hide any overflow content */
}

.info-flex {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

.info-box:hover {
  transform: translateY(-5px);
}

.info {
  padding: 40px;
  background-color: #e9fbff;
}
.info h1 {
  font-family: "Rubik", sans-serif;
  font-weight: bold;
  font-size: 28px; /* Adjust to your preference */
  line-height: 1.2;
  margin-bottom: 16px; /* Space below the h1 */
  letter-spacing: -0.5px; /* Slightly tighter tracking for modern look */
  color: #0a1f3c; /* Making the color consistent with other elements, but you can adjust this */
}

.info p {
  font-family: "Rubik", sans-serif;
  font-size: 18px; /* Adjust to your preference */
  line-height: 1.4;
  margin-bottom: 24px; /* Space below the paragraph before the info boxes */
  color: #555; /* Slightly muted color for secondary text */
  letter-spacing: 0.5px; /* A touch of spacing for improved readability */
}

.info h3 {
  margin: 0;
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.info h3 i {
  margin-bottom: 8px;
  font-size: 1.8em;
  align-items: center;
  justify-content: center;
}

/* Adjusting the content inside the .info-box to fit well */
.info-box img {
  max-height: 60%; /* Adjust as necessary */
  object-fit: cover;
}

.info-box h3 {
  font-size: 1.2em;
  margin: 0;
  align-items: center;
  justify-content: center;
}

.info-box p {
  font-size: 0.8em;
  margin: 0;
  align-items: center;
  justify-content: center;
  text-align: center; /* This will center the text horizontally */
  width: 100%;
}

.reach {
  display: flex;
  justify-content: space-between; /* Ensure equal spacing */
  flex-wrap: wrap; /* Wrap content on smaller screens */
}

footer {
  background: #3f6a8a; /* Consistent color with header */
  padding: 20px 0; /* Consistent padding with header */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%; /* Adjust this if you want a max width */
  padding: 20px 0;
}

.footer-content h2 {
  font-size: 28px;
  font-weight: bold;
  color: #0a1f3c; /* Or whatever color fits your design */
}

/* No need to add button styles if it's exactly the same as the other one */

#rights {
  text-align: center;
  color: #f1f1f1; /* Slightly brighter text for better contrast */
}

/* Add this CSS rule to achieve the blurred effect using React's conditional rendering */
.page-content-wrapper.blurred {
  filter: blur(4px);
}

#EmilyPopup {
  position: fixed;
  display: flex;
  transform: translate(-50%, -50%);
  height: 220px;
  width: 440px;
  z-index: 1000;
  box-sizing: border-box;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.close-btn {
  position: absolute;
  top: 0;
  right: 20px;
  background: transparent;
  color: #333;
  border: none;
  font-size: 20px;
  cursor: pointer;
  z-index: 1020;
  padding: 5px;
}

.close-btn:focus {
  outline: none;
}

#PopupEmily {
  width: 40%; /* Changed to 30% */
  height: auto;
  object-fit: cover;
  position: relative;
  bottom: -30px;
}

.popup-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 60%;
  margin-top: 0px;
}

.popup {
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  z-index: 1010;
  max-width: 90%;
  overflow: hidden;
}

.popup-content-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
}

#Voice {
  width: 30px;
  height: 30px;
}

.microphone-note {
  font-size: 12px;
  margin-top: 10px;
  font-style: italic;
  color: gray;
}

.info-dump {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 40px 60px;
  background-color: white;
}

.info-dump .content {
  flex: 1;
  margin-right: 180px;
}

.info-dump .content h1 {
  font-size: 32px;
  margin-bottom: 20px;
}

.info-dump .content p {
  font-size: 18px;
  line-height: 1.5;
}

/* Container for the travel image */
.travel-image-box {
  width: 100%; /* You can set a specific width if required */
  max-width: 500px; /* Adjust as needed */
  overflow: hidden; /* To ensure content inside doesn't overflow */
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  position: relative; /* Useful if you're going to position elements inside */
}

/* Styling for the beach image */
.travel-beach-image {
  display: block; /* Removes any unwanted spacing caused by inline elements */
  width: 100%; /* Makes sure the image takes the full width of its container */
  max-height: 100%; /* Limits the image height */
  object-fit: cover; /* Ensures the image scales and crops appropriately */
  transition: transform 0.3s ease; /* Optional: Smoothens any image transformation effects */
}

/* Example: A hover effect to slightly scale the image when hovered. This is optional. */
.travel-image-box:hover .travel-beach-image {
  transform: scale(1.05);
}
